.list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  grid-column-gap: 10px;
  padding: 1em;
}

.flush-cache {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.zaak-card {
  padding: 0.5em;
  border: solid 1px #DDDDDD;
}

.zaak-card + .zaak-card {
  margin-top: .5em;
}

.zaak-status {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 0.5em;
}

.zaak-status--eind .zaak-status__dot {
  background: #c00;
  color: #fff;
}

.zaak-status__dot {
  width: 30px;
  height: 30px;
  padding: 5px;
  border: solid 3px #c00;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 80%;
  margin-right: 1em;
}

.zaak-status__name {
  width: 40%;
}

.zaak-status__timestamp {
  display: block;
  color: #51514e;
  font-size: 80%;
}

.zaak-status__info {
  width: calc(50% - 1em - $dot-size);
}

.zaak-preview {
  padding: 0.5em;
  border: solid 1px #DDDDDD;
}

.zaak-preview .zaak-preview__zaaktype {
  display: block !important;
  font-size: 1em;
  text-transform: uppercase;
}

.zaak-preview .zaak-preview__information {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.zaak-preview + .zaak-preview {
  margin-top: 1em;
}

.log-entries {
  background: #EEEEEE;
  transform: scale(0.9);
  padding: 0.5em 2em;
}

.log-entries * {
  color: #666666;
}

body {
  font-family: 'Fira Sans', 'Helvetica Neue', Arial, sans-serif;
  font-weight: 400;
  line-height: 1.5em;
  font-size: 1rem;
  color: #1d1d1c;
  background-color: #f2f2f2;
  margin: 0;
}

body, body * {
  box-sizing: border-box;
}

.footer {
  font-family: 'Fira Sans', 'Helvetica Neue', Arial, sans-serif;
  font-weight: 400;
  line-height: 1.5em;
  padding-top: 4em;
  padding-bottom: 2em;
  background: url(../img/bg-footer.svg) top center #c00;
  background-size: cover;
  color: #fff;
}

.footer .footer__content {
  max-width: 74em;
  margin: 0 auto;
  padding-right: 15px;
  padding-left: 15px;
}

.footer .footer__column {
  max-width: 33%;
}

.footer .footer__contact-details {
  display: flex;
}

.footer .footer__contact-details > * {
  flex: 1;
}
